/* GLOBAL STYLES -------------------------------------------------- */

:root {
  --primary: #867c9d;
}
/* Padding below the footer and lighter body text */

body {
  color: #5a5a5a;
  font-family: 'Poppins';
  letter-spacing: 1px, 100;
}

/* MENU SYLES */

.textoMenu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  padding-right: 60px;
}

.navbar-light .navbar-nav .nav-link {
  color: black;
  padding-left: 50px;
}

img.logoNav {
  padding-left: 40px;
  padding-top: 5px;
  padding-bottom: 5px;
}

/* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */

/* Carousel base class */
.carousel {
  margin-bottom: 4rem;
}

#carouselDesktop {
  display:block;
}

#carouselMobile {
  display: none;
}
/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  bottom: 3rem;
  z-index: 10;
}

.carouselTamanio {
  padding-right: 60px;
  height: 513px;

}

.texto {
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  color: rgb(71, 69, 69);
  font-size: 100%;
  padding-bottom: 225px;
  padding-right: 50px;
  white-space: pre;

}

.textoHorizontal {
  padding-top: 220px;
  padding-left: 70px;
  font-size: 25px;
  color: rgb(71, 69, 69);
}

.textoChico {
  font-size: 13px;
  margin-top: 32px;
}


.fondoDiv {
  background-color: #efefef;

}

/* Declare heights because of positioning of img element */
.carousel-item {
  height: 32rem;
}

.carousel-item>img {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  height: 100%;
}


/* MARKETING CONTENT
-------------------------------------------------- */

/* Center align the text within the three columns below the carousel */


.marketing {
  margin-top: 70px;
}

.marketing .col-lg-4 {
  margin-bottom: 1.5rem;
  text-align: center;
}

.marketing h2 {
  font-weight: 400;
}


.marketing .col-lg-4 p {
  margin-right: .75rem;
  margin-left: .75rem;
}

/* Featurettes
------------------------- */

.featurette-divider {
  margin: 5rem 0;
  /* Space out the Bootstrap <hr> more */
}

/* Thin out the marketing headings */
.featurette-heading {
  font-weight: 300;
  line-height: 1;
  letter-spacing: -.05rem;
}


