@media (max-width: 576px) {
  .carouselTamanio {
    padding-right: 0px;
    height: 100%;
  }

  div.col-lg-7.carouselTamanio {
    height: 480px;
  }

  .carousel-item>img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  #carouselDesktop {
    display:none;
  }
  
  #carouselMobile {
    display: block;
  }

  .textoHorizontal {
    padding: 0px 0px 0px 20px;
    margin: 0px 10px 0px 10px;
  }

  .texto {
    -webkit-writing-mode: horizontal-tb;
        -ms-writing-mode: lr-tb;
            writing-mode: horizontal-tb;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    color: rgb(71, 69, 69);
    font-size: 100%;
    padding: 70px 0px 10px 35px;
  }

}


@media (max-width: 768px) {
  .carouselTamanio {
    padding-right: 0px;
    height: 100%;
  }

  .carousel-item>img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  #carouselDesktop {
    display:none;
  }
  
  #carouselMobile {
    display: block;
  }

  div.col-lg-7.carouselTamanio {
    height: 480px;
  }

  .textoHorizontal {
    padding: 0px 0px 0px 20px;
    margin: 0px 10px 0px 10px;
  }
  .texto {
    -webkit-writing-mode: horizontal-tb;
        -ms-writing-mode: lr-tb;
            writing-mode: horizontal-tb;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    color: rgb(71, 69, 69);
    font-size: 100%;
    padding: 70px 0px 10px 35px;
  }
}
