/* Logo */

#nav-logo {
    width: 6rem;
}


/* Nav */

ul {
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase; 
}


ul a {
    color: black;
    color: #b9b0d1 !important;

}

/* ul li:hover  {
    transform: scale(1.1);
} */

ul a:hover  {
    text-decoration: none;
    color: #71668a !important;
    /* text-shadow: -1.2px 0px 0px #71668a,-1.2px 0px 0px #71668a; */
    font-weight: bold;
}

/* SocialMedia */

.social-media i:hover{
    transform: scale(1.3);
}

.social-media i{
    color: #71668a;
}
